.skillBars {
    display: flex;
    flex-direction: column; /* Stack bars vertically */
    gap: 15px; /* Add space between bars */
    margin-bottom: 20px;
    width: 100%;
    padding: 20px; /* Add some padding */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 8px #9acd32 ;/* Subtle shadow for depth */
    color: aqua;
  }
  
  .bar {
    transition: width 0.3s ease-in-out, transform 0.3s ease-in-out; /* Smooth transition for width and transform */
    border-radius: 5px; /* Rounded corners for bars */
    overflow: hidden; /* Ensure inner content respects border radius */
    height: 30px; /* Fixed height for bars */
    color: aquas;
  }

.skillName {
    width: 100px; /* Fixed width for skill names */
    color: #a4881b;
    text-align: right;
  }
  
  .bar rect {
    stroke: none; /* Remove stroke */
    height: 100%; /* Ensure full height */
  }
  
  .bar:hover {
    cursor: pointer;
    transform: scale(1.02); /* Slightly enlarge on hover for effect */
  }
  
  .skillDescription {
    font-weight: 400;
    font-size: 1.2rem; /* Slightly larger text */
    max-width: 51rem;
    padding: 0 2rem;
    color: #ffefef; /* Darker text for better visibility on light background */
    margin-bottom: 20px; /* Space below the description */
  }
  .percentage-display {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: #a4881b;
    padding: 5px;
    border-radius: 5px;
    font-size: 0.8rem;
  }
  
  #skills {
    overflow: hidden;
    width: 100%;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
    padding: 20px; /* Add some padding */
    border-radius: 10px; /* Rounded corners */
  }
  .skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

  @media (max-width: 768px) {
    .skillDescription {
      font-size: 1rem; /* Adjust text size on smaller screens */
      padding: 0 1rem;
     
    }
  
    .bar {
      height: 25px; /* Adjust bar height on smaller screens */
    }
  
    .skillBars {
      gap: 10px; /* Adjust gap on smaller screens */
      padding: 10px; /* Adjust padding on smaller screens */
      color: blue;
    }
  }
  